import { useEffect, useRef, useState, useCallback } from 'react';
import Webcam, { WebcamProps } from 'react-webcam';
import './App.css';
import { FaceDetector, FilesetResolver, Detection } from '@mediapipe/tasks-vision';
import Crown from './Crown';

interface BoundingBox {
    originY: number;
    height: number;
}

interface Data {
    detections: Detection[];
}

const videoConstraints: WebcamProps['videoConstraints'] = {
    width: 400,
    height: 400,
    facingMode: 'user',
};

const Profile: React.FC = () => {
    const webcamRef = useRef<Webcam>(null);
    const [position, setPosition] = useState<[number, number]>([0, 0]);
    const [displayIcon, setDisplayIcon] = useState<boolean>(true);
    const [sizeImg, setSizeImg] = useState<[number, number]>([20, 20]);
    const [fps, setFps] = useState<number>(0);
    const [faceDetector, setFaceDetector] = useState<FaceDetector | null>(null);

    const detect = useCallback(async (image: HTMLImageElement): Promise<Data> => {
        if (faceDetector) {
            return faceDetector.detect(image);
        }
        return { detections: [] };
    }, [faceDetector]);

    const mascPosition = useCallback(async () => {
        try {
            const image = new Image();
            image.src = webcamRef.current?.getScreenshot() || '';
            await image.decode(); // Wait for image decoding
            const data = await detect(image);
            if (data && data.detections.length > 0) {
                const boundingBox: BoundingBox | undefined = data.detections[0]?.boundingBox;
                if (boundingBox) {
                    setDisplayIcon(true);
                    const originX = data.detections[0].keypoints[5].x * 400 - boundingBox.height;
                    const originY2 = (boundingBox.originY - boundingBox.height) - boundingBox.height * 0.3;
                    setPosition([originX, originY2]);
                    setSizeImg([boundingBox.height, boundingBox.height])
                }
            } else {
                setDisplayIcon(false);
            }
        } catch (error) {
            setDisplayIcon(false);
        }
    }, [detect]);

    useEffect(() => {
        let isMounted = true;
        let framesCount = 0;
        let startTime = performance.now();

        const intervalId = setInterval(() => {
            if (isMounted) {
                if (document.scripts[1]) {
                    document.scripts[0].remove();
                }
                mascPosition();
                framesCount++;

                const currentTime = performance.now();
                const elapsedTime = currentTime - startTime;

                if (elapsedTime >= 1000) {
                    const calculatedFps = Math.round((framesCount * 1000) / elapsedTime);
                    setFps(calculatedFps);
                    framesCount = 0;
                    startTime = currentTime;
                }
            }
        }, 13); // Set the interval to approximately 33 milliseconds (30 FPS)

        return () => {
            isMounted = false;
            clearInterval(intervalId);
        };
    }, [mascPosition]);

    useEffect(() => {
        const createModels = async () => {
            const vision = await FilesetResolver.forVisionTasks(
                'https://cdn.jsdelivr.net/npm/@mediapipe/tasks-vision/wasm'
            );
            const detector = await FaceDetector.createFromModelPath(
                vision,
                'https://storage.googleapis.com/mediapipe-models/face_detector/blaze_face_short_range/float16/1/blaze_face_short_range.tflite'
            );
            setFaceDetector(detector);
        };

        createModels();

        return () => {
            // Cleanup face detector on component unmount
            if (faceDetector) {
                faceDetector.close();
                setFaceDetector(null);
            }
        };
    }, []);

    return (
        <div>
            <div className="containerWebcam">
                <Webcam
                    id="image"
                    audio={false}
                    height={400}
                    width={400}
                    screenshotFormat="image/jpeg"
                    videoConstraints={videoConstraints}
                    ref={webcamRef}
                />
                <div
                    className="itemCrown"
                    style={{  transition: 'left 0.1s ease-in-out, top 0.1s ease-in-out', left: position[0], top: position[1], display: displayIcon ? 'inherit' : 'none' }}
                >
                    <Crown sizeImg={sizeImg} />
                </div>
            </div>
            <div className="fpsDisplay">FPS: {fps}</div>
        </div>
    );
};

export default Profile;
